import { BucketMetric, Metric, Schema } from '../interfaces';
import { MetricsTags, schemaUtil, utility } from 'o11y/shared';
import {
    BucketHistogram,
    CoreEnvelope,
    CoreEnvelopeContents,
    LogMessage,
    MessageBundle,
    MetricTag,
    StaticAttributes,
    UpCounter,
    ValueRecorder
} from './interfaces';
import { version as schemaVersion } from 'o11y_schema/version';

export const coreEnvelopeKey = 'o11y';
export class CoreEnvelopeBuilder {
    private _envelope: CoreEnvelope;

    constructor() {
        this._envelope = {
            diagnostics: {
                key: coreEnvelopeKey,
                generatedTimestamp: undefined,
                bundleCount: 0,
                bucketHistogramCount: 0,
                upCounterCount: 0,
                valueRecorderCount: 0,
                schemaVersion
            },
            bundles: [] as MessageBundle[],
            metrics: {
                bucketHistograms: [] as BucketHistogram[],
                upCounters: [] as UpCounter[],
                valueRecorders: [] as ValueRecorder[]
            },
            staticAttributes: {}
        };
    }

    withStaticAttributes(staticAttributes: StaticAttributes): CoreEnvelopeBuilder {
        this._envelope.staticAttributes = staticAttributes;
        return this;
    }

    withLogs(schemaName: string, logs: LogMessage[]): CoreEnvelopeBuilder {
        let msgBundle: MessageBundle = this._envelope.bundles.find(
            (bundle) => bundle.schemaName === schemaName
        );

        if (msgBundle) {
            for (const log of logs) {
                msgBundle.messages.push(log);
            }
        } else {
            msgBundle = {
                schemaName: schemaName,
                messages: logs
            };
            this._envelope.bundles.push(msgBundle);
        }
        this._envelope.diagnostics.bundleCount = this._envelope.bundles.length;
        return this;
    }

    private static _getMetricTags<T>(metric: Metric<T>): MetricTag[] {
        const tags: MetricsTags = metric.getTags();
        if (tags) {
            return Object.entries(tags).map((entry) => {
                const mt: MetricTag = {
                    name: entry[0],
                    value: entry[1].toString()
                };
                return mt;
            });
        }
        return undefined;
    }

    static getUpCounters(metrics: Metric<number>[], reset = true): UpCounter[] {
        return metrics.map((metric) => {
            const data: UpCounter = {
                name: metric.getName(),
                createdTimestamp: metric.getCreatedOn(),
                lastUpdatedTimestamp: metric.getLastUpdatedOn(),
                value: metric.getData(),
                ownerName: metric.getOwnerName(),
                ownerAppName: metric.getOwnerAppName(),
                tags: this._getMetricTags(metric)
            };
            if (reset) {
                metric.reset();
            }
            return data;
        });
    }

    static getValueRecorders(metrics: Metric<number[]>[], reset = true): ValueRecorder[] {
        return metrics.map((metric) => {
            const data: ValueRecorder = {
                name: metric.getName(),
                createdTimestamp: metric.getCreatedOn(),
                lastUpdatedTimestamp: metric.getLastUpdatedOn(),
                values: metric.getData(),
                ownerName: metric.getOwnerName(),
                ownerAppName: metric.getOwnerAppName(),
                tags: this._getMetricTags(metric)
            };
            if (reset) {
                metric.reset();
            }
            return data;
        });
    }

    static getBucketHistograms(metrics: BucketMetric<number[]>[], reset = true): BucketHistogram[] {
        return metrics.map((metric) => {
            const data: BucketHistogram = {
                name: metric.getName(),
                createdTimestamp: metric.getCreatedOn(),
                lastUpdatedTimestamp: metric.getLastUpdatedOn(),
                values: metric.getData(),
                buckets: metric.getBuckets(),
                ownerName: metric.getOwnerName(),
                ownerAppName: metric.getOwnerAppName(),
                tags: this._getMetricTags(metric)
            };
            if (reset) {
                metric.reset();
            }
            return data;
        });
    }

    withUpCounters(upCounters: UpCounter[]): CoreEnvelopeBuilder {
        for (const upCounter of upCounters) {
            this._envelope.metrics.upCounters.push(upCounter);
        }
        this._envelope.diagnostics.upCounterCount = this._envelope.metrics.upCounters.length;
        return this;
    }

    withValueRecorders(valueRecorders: ValueRecorder[]): CoreEnvelopeBuilder {
        for (const valueRecorder of valueRecorders) {
            this._envelope.metrics.valueRecorders.push(valueRecorder);
        }
        this._envelope.diagnostics.valueRecorderCount =
            this._envelope.metrics.valueRecorders.length;
        return this;
    }

    withBucketHistograms(bucketHistograms: BucketHistogram[]): CoreEnvelopeBuilder {
        for (const bucketHistogram of bucketHistograms) {
            this._envelope.metrics.bucketHistograms.push(bucketHistogram);
        }
        this._envelope.diagnostics.bucketHistogramCount =
            this._envelope.metrics.bucketHistograms.length;
        return this;
    }

    build(): CoreEnvelope {
        this._envelope.diagnostics.generatedTimestamp = utility.time().tsNow;
        return this._envelope;
    }

    static buildFrom(contents: CoreEnvelopeContents): CoreEnvelope {
        const builder = new CoreEnvelopeBuilder();

        if (contents.staticAttributes) {
            builder.withStaticAttributes(contents.staticAttributes);
        }
        if (contents.messages) {
            contents.messages.forEach((logs: LogMessage[], schema: Schema) => {
                builder.withLogs(schemaUtil.getSchemaId(schema), logs);
            });
        }
        if (contents.upCounters) {
            builder.withUpCounters(contents.upCounters);
        }
        if (contents.valueRecorders) {
            builder.withValueRecorders(contents.valueRecorders);
        }
        if (contents.bucketHistograms) {
            builder.withBucketHistograms(contents.bucketHistograms);
        }

        return builder.build();
    }
}
